.header {
  background-color: black;
  color: #ffffff;
  @media screen and (max-width: 800px) {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    // right: 0;
  }
  .shadow_nav {
    display: none;
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba($color: #000000, $alpha: 0.6);
    z-index: -5;
    &_open {
      display: block;
    }
    // @media screen and (max-width: 800px) {
    //   display: block;
    // }
  }
  
}

.wrapper__header, .wrapper__donate {
  display: flex;
  height: 80px;
  align-items: center;
  @media screen and (max-width: 1200px) {
    padding-left: 30px;
    padding-right: 30px;
  }
  @media screen and (max-width: 800px) {
    height: 34px;
    padding-left: 19px;
    padding-right: 20px;
  }
  @media screen and (max-width: 400px) {
    padding: 0 10px;
  }
}
